import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { resultadd } from "../store.js";
import Result from "./Result.js";

export default function Search(props) {
  let navigate = useNavigate();
  let dispatch = useDispatch();
  let state = useSelector((state) => state);
  let [val, setVal] = useState("");

  function searchName() {
    let findVal = state.perfume
      .concat(state.mist)
      .filter((a) =>
        a.name.replace(/\s+/g, "").includes(val.replace(/\s+/g, ""))
      );
    return findVal;
  }

  function findSearch() {
    let findVal = searchName();
    dispatch(resultadd(findVal));

    if (val.length > 0) {
      navigate("/result");
      setVal("");
      props.setSearch("");
    } else if (val.length <= 0) {
      return alert("검색어를 입력해주세요");
    }
  }

  return (
    <div
      className={`search-container ${props.search}`}
      onKeyUp={(e) => {
        if (e.key === "Enter") {
          findSearch();
        }
      }}
      onClick={(e) => {
        e.stopPropagation();
      }}
    >
      <input
        type="text"
        placeholder="SEARCH"
        className="searchinput"
        value={val}
        onChange={(e) => {
          setVal(e.target.value);
        }}
      ></input>
      <span
        className="search-del"
        onClick={() => {
          setVal("");
        }}
      >
        del
      </span>
      <span
        className="search-search"
        onClick={() => {
          findSearch();
        }}
      >
        search
      </span>
      <Result val={val} />
    </div>
  );
}
